.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

#token{
  padding: 10px 0px 10px 0px;
}

#token input{
  height: 8px;
  border: solid black 2px;
  border-radius: 5px;
}

.uploader{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}

